<script>
   import axios  from 'axios'
   import Swall from 'sweetalert2'
   

   export default{
      data(){
         return{
            name: "",
            email: "",
            message: "", 
            
         }

    
      },

      methods:{
         async saveData() {
            try{
               await axios.post("http://localhost:5000/contact",{
                  name: this.name,
                  email: this.email,
                  message: this.message,
               });
               this.name = "";
               this.email = "";
               this.message = "";
               Swall.fire({
                  title: "Done",
                  text: "Message sent successfully", 
                  icon: "success", 
                  confirmButtonColor: "#EB3F57",
                  confirmButtonText: "Continue"
               })
            } catch(err) {
               Swall.fire({
                  title: "Failed!",
                  text: "Internal server error", 
                  icon: "error", 
                  confirmButtonColor: "#EB3F57",
                  confirmButtonText: "Continue"
               })
               console.log(err)
            }
         }
      }

   }
</script>

